						</svg><span class="tracking-[0.15em]">切换网络</span></button>
					<button v-if="walletAddress && !isChainCorrect"
						class="btn-connect flex items-center justify-center gap-2 px-4 py-2 pr-4 text-sm bg-active"><svg
							xmlns="http://www.w3.org/2000/svg" class="h-5 w-5" fill="none" divBox="0 0 24 24"
							stroke="currentColor">
							<path stroke-linecap="round" stroke-linejoin="round" stroke-width="2"
								d="M17 9V7a2 2 0 00-2-2H5a2 2 0 00-2 2v6a2 2 0 002 2h2m2 4h10a2 2 0 002-2v-6a2 2 0 00-2-2H9a2 2 0 00-2 2v6a2 2 0 002 2zm7-5a2 2 0 11-4 0 2 2 0 014 0z">
							</path>
						</svg><span class="tracking-[0.15em]">{{ walletAddressShow }}</span></button>
				</div>
			</header>
			<div class="px-4 py-8 pt-20" style="padding-top: 180px;">
				<div class="mb-10">
					<h2 class="font-extrabold mb-3 text-3xl">每 日 训 练</h2>
					<p v-if="!isRegistered" class="text-gray-100 mb-6 text-xs">训练社区建设获得更多奖励</p>
					<p v-if="isRegistered" class="text-gray-100 mb-6 text-xs">训练社区建设获得更多奖励</p>
					<van-button v-if="user && !isRegistered" @click="showBind = true"
						class="btn-primary w-2/3 mx-auto py-3 text-black font-bold relative bg-active"><span>绑定邀请人</span></van-button>
					<van-button v-if="isRegistered && !isCanFired" :disabled="true"
						class="btn-primary w-2/3 mx-auto py-3 text-black font-bold relative bg-active">
						<!-- <span>暂不可训练</span> -->
						<van-count-down :time="timeCountDown" style="color:#999" format="HH 时 mm 分 ss 秒" />
					</van-button>
					<van-button v-if="isRegistered && isCanFired" @click="checkIn" :disabled="isFireLoading"
