					style="background: linear-gradient(135deg, rgb(42, 47, 47) 0%, rgb(29, 34, 35) 100%);">
					<div class="flex items-center justify-between mb-4">
						<h3 class="font-bold text-left text-lg">已获得 AI 代币
						</h3><svg xmlns="http://www.w3.org/2000/svg" class="h-6 w-6 cursor-pointer" fill="none"
