<script>
import {
	mapState
} from 'vuex'
export default {
	// eslint-disable-next-line vue/multi-word-component-names
	name: 'Home',
	data: () => ({
		toastShow: false,
		toastText: '',
		isLoading: false,
		showSelectBox: false,
		// isCanFired: true,
		inviteAddress: "",
		showBind: false,
		isFireLoading: false,
		isClaimLoading: false,
	}),
	computed: {
		...mapState(['user', 'walletAddress', 'isConnected', 'isChainCorrect', "isRegistered", 'walletAddress', 'poolInstance']),
		walletAddressShow() {
			return this.walletAddress.substr(0, 4) + "..." + this.walletAddress.substr(-4)
		},
		isCanFired() {
			if (this.user) {
				let lastCheckIn = this.user.lastCheckIn;
				let checkInCountToday = this.user.checkInCountToday;
				if ((new Date().getTime() - Number(lastCheckIn) * 1000 > this.$appConfig.fireInterval) && checkInCountToday < this.$appConfig.maxFireCount) {
					return true;
				} else {
					return false;
				}
			} else {
				return false;
			}
		},
		timeCountDown() {
			if (this.user) {
				let lastCheckIn = this.user.lastCheckIn;
				let checkInCountToday = this.user.checkInCountToday;
				if (checkInCountToday == 1) {
					console.log(this.$appConfig.fireInterval - new Date().getTime() + Number(lastCheckIn) * 1000)
					return this.$appConfig.fireInterval - new Date().getTime() + Number(lastCheckIn) * 1000
				}
				if (checkInCountToday == 2) {
					return this.tomorrowTimestamp() - Number(lastCheckIn) * 1000
				}
				return 0;
			} else {
				return 0;
			}
		},
		isPrepared() {
			return this.isCanFired && this.walletAddress && this.isRegistered;
		},
		rewardCount() {
			console.log(this.user)
			if (this.user) {
				console.log(this.user.tokenRewardCount)
				return this.user.tokenRewardCount || 0;
			} else {
				return 0;
			}
		}
	},
	created() {
		let address = this.$getQueryString("address");
		if (address) {
			this.inviteAddress = address;
		}
	},
	methods: {
		tomorrowTimestamp() {
			const now = new Date();
			const tomorrowMidnight = new Date(
				now.getFullYear(),
				now.getMonth(),
				now.getDate() + 1, // 增加1天
				0, 0, 0, 0         // 时、分、秒、毫秒归零
			);
			return tomorrowMidnight.getTime();
		},
		showToast(text) {
			this.toastText = text;
			this.toastShow = true;
			setTimeout(() => {
				this.toastShow = false;
				this.toastText = "";
			}, 2000);
		},
		bindAction() {
			this.isLoading = true;
			this.poolInstance && this.poolInstance.register(this.inviteAddress)
				.then(async (tx) => {

					const receipt = await tx.wait();
					if (receipt.status === 1) {
						this.showToast("绑定成功");
						console.log("绑定成功！區塊號:", receipt.blockNumber);
						this.$store.dispatch("getUserInfo");
						this.showBind = false;
					} else {
						console.log(receipt.status);
					}
					this.isLoading = false;
				}).catch(err => {
					this.isLoading = false;
					console.log("绑定失败", err);
					this.showToast("绑定失败");
				});
		},
		checkIn() {
			this.isFireLoading = true;
			this.poolInstance && this.poolInstance.checkIn().then(async (tx) => {

				const receipt = await tx.wait();
				if (receipt.status === 1) {
					this.showToast("点火成功");
					console.log("点火成功！區塊號:", receipt.blockNumber);
					this.$store.dispatch("getUserInfo");
					this.showBind = false;
				} else {
					console.log(receipt.status);
				}
				this.isFireLoading = false;
			}).catch(err => {
				this.isFireLoading = false;
				console.log("点火失败", err);
				this.showToast("点火失败");
			});
		},
		claimReward() {
			this.$router.push("/data");
			return;
			this.isClaimLoading = true;
			this.poolInstance && this.poolInstance.claimReward().then(async (tx) => {
				console.log(tx);

				const receipt = await tx.wait();
				if (receipt.status === 1) {
					this.showToast("领取成功");
					console.log("领取奖励成功！區塊號:", receipt.blockNumber);
					this.$store.dispatch("getUserInfo");
					this.showBind = false;
				} else {
					console.log(receipt.status);
				}
				this.isClaimLoading = false;
			}).catch(err => {
				this.isClaimLoading = false;
				console.log("领取失败", err);
				this.showToast("领取失败");
			});
		}
	}
};
</script>

